:root {
    --mainTheme: #ff5e14;
    --mainBorder: #cf4200;
    --yellowTheme: rgb(240, 240, 0);
    --yellowBorder: rgb(177, 177, 2);


    --white: #ffffff; 
  }
  

.header{
    min-width: 100%;
    max-width: 100vw;
    min-height: 1080px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

}
.headerSection{
    display: flex;
    /* background: url('../Images/Gym2.jpg'); */
    /* background-size: cover; */
    min-height: 600px;
    min-width: 100%;
    max-width: 100vw;
    top: 300px;
    /* min-height: 1080px; */
    font-family: 'Monoton', cursive;

}

.header-content{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    min-width: 100%;
    background-color: rgba(122, 122, 122, 0.205);
}

.headImg{
    min-width: 33.33333333333333%;
    min-height: 400px;
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
}
.center-div{
    width: 100%;
    display: flex;
    justify-content: center;
    align-content: center;
    background-color:rgba(0, 0, 0, 0.5);

}

.headImg p{
    position: absolute;
    transform: translate(50%,-50%);
    text-transform: uppercase;
    font-size: 2em;
    font-weight: 100;
    display: contents;
    justify-content: center;
    align-content: center;
}
.headImg:first-child, .headImg:last-child{
    /* color: rgb(0, 138, 0);*/
    color: var(--mainTheme) ;
    /* text-shadow: 0 0 3px #b5b800, 0 0 5px #fffb00; */


}

.img1{
    background: url('../Images/Gym1.jpg');
    background-size: cover; 
    background-position-x: center;
}
.img2{
    background: url('../Images/Gym2.jpg');
    background-size: cover; 
    background-position-x: 55%;
}
.img3{
    background: url('../Images/Gym3.jpg');
    background-size: cover; 
    background-position-x: center;
}



.aerro {
    width: 0;
    height: 0;
    border-left: 25vw solid transparent;
    border-right: 25vw solid transparent;
    border-bottom: 100px solid rgb(0, 0, 0);
    margin-top: -100px;
  }

.body-thing{
    display: flex;
    flex-direction: column;
    max-width: 100vw;
    min-width: 100%;
    align-items: center;
    justify-content: space-around;
    margin-top: -50px;
    background-color: black;


}

.join-now{
    width: 100%;
    background-color: transparent;
    display: flex;
    flex-direction: column;
}
.join-now div:first-child{
    font-size: 2rem;
}

.btn-container{
    display: flex;
    justify-content: space-around;
    width: 50%;
    padding: 12px;
}
.btn-border-div{
    width: 90%;
    min-height: 50px;
    margin: 12px;
    padding:8px 8px 8px 8px;
    border: 2px solid #cf4200;
    border-radius: 5px;
    transition: 0.3s;


}

.btn-border-div button{
    width: 100%;
    font-size: 1.4rem;
    background-color:var(--mainTheme) ;
    border:none;
    height: 100%;
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
    border-radius: 5px;
    transition: 0.3s;
}
.btn-border-div:last-child button{
    background-color:transparent ;
    color: white;
}
.btn-border-div:hover{
    border: 2px solid #cf4200;
    border-radius: 5px;
    background-color:#ff5e14 ;

}
.btn-border-div:hover button{
    background-color: rgba(240, 240, 0, 0) ;
    color: black;
}




.card{
    min-height: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 50%;
    margin-top: 0px;
    padding: 5px;
    margin-bottom: 20px;
    max-width: 98%;
}
.card-1{
    background: url('../Images/wallapaper1.jpg');
    background-size: cover;
}
.card-2{
    background: url('../Images/wallapaper2.jpg');
    background-size: cover;
}

.card-3{
    background: url('../Images/wallapaper3.jpg');
    background-size: cover;
}
.card-4{
    background: url('../Images/wallapaper4.jpg');
    background-size: cover;
}

.card-text{
    width: 40%;
    height: 150px;
    /* background-color: rgba(65, 65, 0, 0.726) ; */
    background-color: #cf42007c;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    /* border: 2px solid rgb(255, 255, 0); */
    border: 2px solid #cf4200;
    
    font-size: 1.3rem;
    text-decoration: underline overline;}


@media only screen and (max-width: 700px){
    .headerSection{
        flex-direction: column;
    }
    .headImg{
        min-height: 200px;
    }
    .aerro{
        margin-top: -40px;
    }
    .btn-container{
        width: 90%;
    }
    .card{
        width: 95%;
    }
    .card-text{
        width: 60%;
    }
    
}