
.navContainer{
    display: flex;
    position: fixed;
    z-index: 1;
    min-width: 100vw;
    justify-content: space-between;
    align-items: center;
    min-height: 50px;
    max-height: 50px;
    max-width: 100vw;
    background-color: transparent;
    padding-top: 10px;
}
.navContainer.active{
    position: fixed;
    background-color: #2523229f;
    
}
.logo-container{
    width: 40%;
    display: flex;
    
}
.nav-logo{
    max-width: 100px;
    max-height: 100px;
    background-color: white;
    border-radius: 5px;
    border-bottom-right-radius: 65px;
    transition: 0.2s;
}
.nav-logo:hover{
    background-color: #e64900;
}


.nav-links{
    display: flex;
    width: 40%;
    justify-content: space-around;
    font-size: 12px;
}
.nav-links a{
    text-decoration: none;
    color: rgb(255, 255, 255);
    transition: 0.2s;
    
}
.nav-links a:hover{
    color: #e64900;
}

@media only screen and (max-width: 700px){

    .nav-links{
        font-size: 8px;
    }
}

.fixedn{
    min-height: 50px;
}